@import '../untils/variables';

.list-answers {
  height: 110px;
  margin: 0;
  padding: 10px 5px;
  background: $list-answers-background;
  overflow: auto;
  border: 1px solid $list-answers-lid-border;
  border-radius: 6px;

  &--lid {
    border-right: none;
    border-bottom: none;
    border-left: none;
    border-radius: 0;
  }

  &--reservation {
  }

  &--create-debtor {
    height: auto;
    text-align: start;
  }

  &--change-debtor {
    height: auto;
    text-align: start;
  }

  &__item {
    display: flex;
    list-style: none;
    margin-bottom: 5px;
    gap: 5px 10px;

    &--lid {
      gap: 5px;
    }

    &--reservation {
      display: flex;
      align-items: center;
      justify-content: start;
      gap: 5px;
      margin-bottom: 10px;
      font-size: 13px;
      line-height: 1.4;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__button {
    flex-shrink: 0;
    border: none;
  }

  &__title {
    &--name {
      margin-right: auto;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    &--time {
      flex-shrink: 0;
      width: 90px;
      text-align: center;
      white-space: nowrap;
    }
    &--period {
      flex-shrink: 0;
      width: 75px;
      white-space: nowrap;
    }
  }

  &__item-title {
    padding-bottom: 3px;
    font-size: 15px;
    font-weight: bold;
  }

  &__item-id {
    max-width: 50px;
    min-width: 50px;
  }

  &__item-date {
    max-width: 95px;
    min-width: 95px;
    text-align: center;
  }

  &__item-contact {
    max-width: 190px;
    min-width: 190px;
  }

  &__item-comment {
    max-width: 250px;
    min-width: 250px;
    text-align: start;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__item-open {
    max-width: 55px;
    min-width: 55px;
  }

  &__item-close {
    padding-top: 4px;
    color: #444444;
  }

  &__item-sign {
    max-width: 130px;
    min-width: 130px;
    text-align: start;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__item-debtor-number {
    max-width: 130px;
    min-width: 130px;
    text-align: start;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__item-name {
    max-width: 190px;
    min-width: 190px;
    text-align: start;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__item-phone {
    max-width: 120px;
    min-width: 120px;
    text-align: start;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__item-address {
    max-width: 210px;
    min-width: 210px;
  }

  &__item-date-birth {
    max-width: 75px;
    min-width: 75px;
    text-align: start;
  }

  &__item-email {
    max-width: 180px;
    min-width: 180px;
    text-align: start;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__item-unp {
    max-width: 120px;
    min-width: 120px;
    text-align: start;
    white-space: nowrap;
  }
}
