@import '../untils/variables';

.list-spares {
  width: 1050px;
  max-height: 800px;
  height: auto;
  transform: translateY(30%);
  padding: 30px;

  &__title {
    margin: 0 0 30px;
    padding: 0;
    font-size: 15px;
    font-weight: bold;
  }

  &__table {
    display: table;
    border-collapse: collapse;
    background: $recording-bg;
    margin: 0;
    padding: 0 30px 0 0;
  }

  &__table-row {
    &:hover {
      background: rgba(38, 139, 210, 0.1);
    }

    &--header {
      background: $table-date-background-color;
    }
  }

  &__table-cell {
    padding: 5px;
    text-align: start;
    border: 1px solid $table-border-color;

    &--material-no {
      width: 135px;
      white-space: nowrap;
    }

    &--name-material {
      width: auto;
    }

    &--quantity {
      width: 60px;
    }

    &--unit {
      width: 40px;
      text-align: center;
    }

    &--price {
      width: 100px;
    }
  }
}
