@import '../untils/variables';

.history {
  width: 1100px;
  max-height: 800px;
  height: auto;
  transform: translateY(11%);
  padding: 30px 0 45px 30px;

  &__title {
    margin: 0 0 30px;
    padding: 0;
    font-size: 15px;
    font-weight: bold;
  }

  &__service {
    margin: 0;
    padding: 0 30px 0 0;
    overflow: auto;
    list-style: none;
  }

  &__item {
    margin-bottom: 30px;
    padding: 10px 20px;
    background: $form-background-color;
    border: 1px solid $form-input-border-color;
    border-radius: 6px;
  }

  &__header {
    display: flex;
    gap: 30px;
    margin-bottom: 10px;
  }

  &__info {
    margin-bottom: 30px;
  }

  &__list {
    list-style: none;
    background: $recording-bg;
  }

  &__list-header {
    display: flex;
    list-style: none;
    margin-bottom: 5px;
    gap: 5px;
  }

  &__list-info {
    display: flex;
    list-style: none;
    margin-bottom: 5px;
    gap: 5px;
  }

  &__button {
    margin: 20px 0 10px auto;
  }

  &__fieldset {
    width: 150px;
  }

  &__fieldset-info {
    align-items: start;
  }

  &__table {
    display: table;
    border-collapse: collapse;
    background: $recording-bg;
  }

  &__table-row {
    &:hover {
      background: rgba(38, 139, 210, 0.1);
    }

    &--header {
      white-space: nowrap;
      background: $table-date-background-color;
    }
  }

  &__table-cell {
    padding: 5px;
    min-width: 73px;
    text-align: start;
    border: 1px solid $table-border-color;

    &--leaning {
      width: 330px;
    }
  }
}
