@import '../untils/variables';

.orders {
  width: 830px;
  max-height: 800px;
  height: auto;
  transform: translateY(11%);
  padding: 30px 0 45px 30px;
  overflow: auto;

  &__lid {
    padding-bottom: 30px;
    height: auto;
  }

  &__deal {
    height: auto;
  }

  &__title {
    margin: 0 0 30px;
    padding: 0;
    font-size: 15px;
    font-weight: bold;
  }

  &__list {
    margin: 0;
    padding: 0 30px 0 0;
    background: #fff;
  }

  &__item {
    display: flex;
    justify-content: start;
    list-style: none;
    background: $table-border-color;
    border: 1px solid $table-border-color;
  }

  &__item-open {
    display: flex;
    list-style: none;
    background: $basic-white;
    color: $basic-dark;
    width: 100%;

    &:hover {
      background: rgba(38, 139, 210, 0.1);
    }
  }

  &__item-name {
    max-width: 300px;
    min-width: 300px;
    border-right: 1px solid $table-border-color;
  }

  &__item-data {
    max-width: 95px;
    min-width: 95px;
    text-align: center;
    border-right: 1px solid $table-border-color;
  }

  &__item-id {
    min-width: 50px;
    max-width: 50px;
    border-right: 1px solid $table-border-color;
  }

  &__item-responsible {
    max-width: 300px;
    min-width: 300px;
  }
}
