@import '../untils/variables';

.table {
  display: none;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: auto;
  font-size: 14px;
  line-height: 24px;
  border-collapse: collapse;
  text-align: center;
  overflow: hidden;

  &--show {
    display: table;
  }

  &__row {
    width: 100vh;
  }

  &__cell {
    padding: 5px;
    line-height: 1.4;
    text-align: center;
    border: solid $table-border-color;
    border-width: 0 0 1px 1px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    max-width: 50px;
    min-width: 50px;

    &:last-child {
      border-right-width: 1px;
    }

    &--time {
      color: $table-time-color;
      background: $table-time-background-color;
      border-color: $table-time-border-color;
    }

    &--type {
      font-weight: 500;
      text-align: start;
      font-size: 17px;
      background: $table-date-background-color;
    }

    &--staff {
      text-align: start;
      min-width: 120px;

      &:hover {
        overflow: visible;
      }
    }

    &--active {
      background: $table-cell-selected;

      & + & {
        border-left-color: $table-cell-selected;
      }
    }
  }
}
